import burger from "./modules/burger";
import accordion from "./modules/accordion";
import smoothScroll from "./modules/smooth-scroll";
import learningMaterialsArr from "./data/learningMaterialsArr";

window.addEventListener('DOMContentLoaded', () => {
    accordion(
        learningMaterialsArr,
        '.learning__wrapper',
        '.learning__descr',
        '.learning__btn',
        '.learning__title'
    );
    burger({
        menuSelector: '.header__menu',
        menuItemSelector: '.header__item',
        hamburgerSelector: '.hamburger',
        menuSelectorActive: 'header__menu_active',
        hamburgerSelectorActive: 'hamburger_active'
    });
    smoothScroll('.pageup', 500);
});