const accordion = (materealsArr, wrapperSelector, descrSelector, btnSelector, titleSelector) => {
    const wrappers = document.querySelectorAll(wrapperSelector);

    materealsArr.forEach((matereal, i) => {
        const descr = document.createElement('div');
        descr.classList.add(descrSelector.slice(1));
        descr.innerHTML = `
            <p>${matereal.descr}</p>
        `

        const wrapper = wrappers[i];
        if (wrapper) {
            wrapper.insertAdjacentElement('afterend', descr);
        } else {
            console.error(`Элемент wrapper с индексом ${i} не найден.`)
        }
    });

    const titlesToggle = document.querySelectorAll(titleSelector),
          btnsToggle = document.querySelectorAll(btnSelector),
          descriptions = document.querySelectorAll(descrSelector);

    if (titlesToggle.length === btnsToggle.length && descriptions.length) {
        const showDescr = (i) => {
            btnsToggle[i].classList.toggle('active');
            descriptions[i].classList.toggle('showDescr');
        };
        titlesToggle.forEach((title, i) => {
            title.addEventListener('click', () => showDescr(i));
        });
        btnsToggle.forEach((btn, i) => {
            btn.addEventListener('click', () => showDescr(i));
        });
    } else {
        console.log(titlesToggle);
        console.log(btnsToggle)
        console.error('Количество элементов не совпадает')
    }

    // learningMaterealsArr.forEach(matereal => {
    //     if (!matereal.descr) {
    //         const title = document.createElement('h2');
    //         title.textContent = matereal.title;
    //         container.append(title);
    //     } else {
    //         const wrapper = document.createElement('div');
    //         wrapper.classList.add('learning__wrapper');
    //         wrapper.innerHTML = `
    //         <h2 class="learning__title">${matereal.title}</h2>
    //         <button class="learning__btn" type="button"><span></span><span></span></button>
    //         `;
    //         const descr = document.createElement('div');
    //         descr.classList.add('learning__descr');
    //         descr.innerHTML = `
    //         <p>${matereal.descr}</p>
    //         `
    //         container.append(wrapper);
    //         container.append(descr);
    //     }

    // });
};

export default accordion;